import React, { Component } from "react";
import styled from "styled-components";
import GlobalStyles from "../../assets/styles/GlobalStyles";
import { Title } from "../../components/Title/Title";
import IconIMG from "../../components/IconIMG/IconIMG";
import IconSVG from "../../components/IconSVG/IconSVG";
import { Article } from "../../components/Article/Article";
import contactGirl from "../../assets/images/contact-template-girl.jpg";
import grapesGirl from "../../assets/images/girl-in-grapes.jpg";
import blob from "../../assets/images/blob.svg";
import blob3 from "../../assets/images/blob3.svg";
import dots from "../../assets/images/dots2.svg";
import handsWithGrapes from "../../assets/images/hands-with-grapes.jpg";
import Footer from "../../templates/Footer/Footer";
import GoogleMaps from "../../components/GoogleMapsContainer/GoogleMapsContainer";

const Contact = styled.section`
  background-color: white;
  color: black;
  overflow: hidden;
  position: relative;
  background-color: #e5dede;

  .grapes-hands {
    width: 90%;
    margin: 0 auto;
    box-shadow: 6px 6px 8px 0px rgba(0, 0, 0, 0.29);
  }
`;

const Header = styled.header`
  min-height: 65vh;
  position: relative;
  width: 100%;
  margin-top: 70px;

  h1 {
    bottom: 15px;
    left: 20px;
    width: 100%;
    align-self: flex-start;
    font-size: 27px;
    @media only screen and (orientation: portrait) and (min-width: 375px) {
      bottom: 43px;
    }
    @media only screen and (orientation: portrait) and (min-width: 414px) {
      bottom: 63px;
    }
    span {
      position: relative;
      display: block;
      ::after {
        position: absolute;
        top: 55%;
        left: 10%;
        z-index: -1;
        display: block;
        content: "";
        background-color: #cd97c4;
        width: 50%;
        height: 2px;
      }
    }
  }
`;

const PicsContainer = styled.div`
  position: relative;
  min-height: 300px;
  height: 90%;
  .contact-girl {
    position: relative;
    width: 90%;
    z-index: 3;
    box-shadow: 6px 6px 8px 0px rgba(0, 0, 0, 0.29);
  }
  .grapes-girl {
    position: absolute;
    top: 20%;
    right: 0;
    width: 50%;
    margin-right: 0;
    z-index: 3;
    box-shadow: 6px 6px 8px 0px rgba(0, 0, 0, 0.29);
  }

  .blob {
    position: absolute;
    width: 80%;
    bottom: 0;
    right: 0;
    @media only screen and (orientation: portrait) and (min-width: 375px) {
      bottom: -33px;
      right: 20px;
    }
    @media only screen and (orientation: portrait) and (min-width: 414px) {
      bottom: -67px;
    }
  }

  .blob:nth-of-type(2) {
    bottom: 20px;
    transform: rotate(90deg);
    @media only screen and (orientation: portrait) and (min-width: 375px) {
      bottom: -14px;
      right: 0;
    }
    @media only screen and (orientation: portrait) and (min-width: 414px) {
      bottom: -45px;
    }
  }

  .dots {
    position: absolute;
    bottom: 50px;
    left: 30px;
    width: 20%;
    z-index: 4;
    @media only screen and (orientation: portrait) and (min-width: 375px) {
      bottom: 10px;
    }
    @media only screen and (orientation: portrait) and (min-width: 414px) {
      bottom: -11px;
    }
  }
`;

const StyledArticle = styled(Article)`
  position: relative;
  margin: 20px auto 50px;

  p {
    padding-bottom: 40px;
  }

  span {
    position: absolute;
    /* visibility: visible; */
    display: block;
    bottom: 0;
    left: 0;
    content: "";
    width: 100%;
    height: 3px;
    background-color: #fff;
  }
  span:nth-child(2) {
    transform: translateY(-250%);
  }
`;

const ContactInfo = styled.div`
  font-family: "Montserrat", sans-serif;
  color: #666;
  margin: 50px 20px;

  .hours p:nth-of-type(1) {
    margin-bottom: 15px;
  }
  .phone,
  .mail {
    margin-top: 40px;
  }
  h2 {
    padding-bottom: 20px;
  }

  p,
  a {
    font-weight: 600;
    font-size: 14px;
  }

  a {
    position: relative;
    text-decoration: none;
    color: #666;
    padding-bottom: 5px;
    ::after {
      bottom: 0;
      left: 0;
      position: absolute;
      width: 100%;
      height: 2px;
      content: "";
      display: block;
      background-color: #666;
    }
  }
`;

const StyledGM = styled.div`
  position: relative;
  width: 100%;
  height: 400px;
  background-color: #666;
`;

class ContactTemplate extends Component {
  state = {};

  render() {
    return (
      <>
        <Contact>
          <GlobalStyles isScrollable={this.props.isScrollable} />
          <Header>
            <PicsContainer className={"pics-container"}>
              <IconIMG
                className={"contact-girl"}
                src={contactGirl}
                alt={"dziewczyna po prostowaniu"}
              />
              <IconIMG
                className={"grapes-girl"}
                src={grapesGirl}
                alt={"dziewczyna w winogronach"}
              />
              <IconSVG className={"blob"} src={blob} />
              <IconSVG className={"blob"} src={blob3} />
              <IconSVG className={"dots"} src={dots} />
            </PicsContainer>
            <Title>
              najwyższa jakość usług
              <br /> w najdogodniejszy
              <span>sposób</span>
            </Title>
          </Header>
          <StyledArticle>
            <p>
              Zabiegi wykonuję z dojazdem do klienta na terenie{" "}
              <strong>
                Będzina, Czeladzi, Dąbrowy Górniczej, Sosnowca, Katowic i okolic
              </strong>
              , a jedyne czego potrzebuję do pracy to dostępu do wody, prądu i
              krzesła.
            </p>
            <span />
            <span />
          </StyledArticle>
          <IconIMG
            className={"grapes-hands"}
            src={handsWithGrapes}
            alt={"dłonie z winogronami"}
          />
          <ContactInfo>
            <div className="hours">
              <h2>Godziny Otwarcia</h2>
              <p>
                Poniedziałek-Piątek <br />
                16:00 - 20:00
              </p>
              <p>
                Sobota-Niedziela <br />
                9:00 - 14:00
              </p>
            </div>
            <div className="phone">
              <h2>Telefon</h2>
              <a href={"tel:555-666-777"}>555-666-777</a>
            </div>
            <div className="mail">
              <h2>Mail</h2>
              <a href={"mialto:biuro@aleproste.pl"}>biuro@aleproste.pl</a>
            </div>
          </ContactInfo>
        </Contact>
        <StyledGM className={"google-maps"}>
          {" "}
          <GoogleMaps />
        </StyledGM>
        <Footer />
      </>
    );
  }
}

export default ContactTemplate;
