import React, { Component } from "react";
import { Map, InfoWindow, Marker, GoogleApiWrapper } from "google-maps-react";

const mapStyles = {
  width: `100%`,
  height: "400px",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)"
};

export class GoogleMaps extends Component {
  render() {
    return (
      <Map
        google={this.props.google}
        zoom={17}
        style={mapStyles}
        initialCenter={{ lat: 50.315593, lng: 19.11279 }}
      >
        <Marker position={{ lat: 50.315593, lng: 19.11279 }} />
        <InfoWindow>
          <div>
            <h1>baaaaaa</h1>
          </div>
        </InfoWindow>
      </Map>
    );
  }
}

export default GoogleApiWrapper({
  apiKey: "AIzaSyBU8lUG2ezwQ3N0kNTjoRY4PdCtC2nB9PY"
})(GoogleMaps);
